@import '_var';


.sp_only{
  @include pc{
    display: none;
  }
}

.pc_only{
  @include sp{
    display: none;
  }
}

body{
  background-color: $white;
  font-family: 'Noto Sans JP',
                "Helvetica Neue",
                Arial,
                "Hiragino Kaku Gothic ProN",
                "Hiragino Sans",
                Meiryo,
                sans-serif;
  .container{
    display: flex;
    width: $width;
    margin: auto;
    @include sp(){
      display: block;
    }
    main{
      margin-bottom: $margin-m;
      @include width_margin('L', $margin-m, 6.5, 3.5);
      @include sp(){
        @include clear_width_margin('L');
      }
    }
    section.side{
      margin-bottom: $margin-m;
      @include width_margin('R', $margin-m, 6.5, 3.5);
      @include sp(){
        @include clear_width_margin('R');
      }
    }
  }
  .title{
    transition: $transition;
    &:hover{
      text-decoration: underline;
      @include hover_opacity();
    }
  }
  section.cats{
    margin: $margin-m 0;
    ul{
      display: flex;
      flex-wrap: wrap;
      font-size: $font-s;
      .cat{
        padding: 5px 25px;
        margin: 0 $margin-s $margin-s 0;
        @include background_purple();
        @include hover_opacity();
      }
    }
  }
}

header{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  padding: $margin-m 0;
  border-bottom: $line;
  width: $width;
  margin: auto; 
  .wrap_logo{
    width: 200px;
    img{
      width: 100%;
    }
  }
  section.links{
    display: flex;
    align-items: flex-end;
    @include sp(){
      flex-wrap: wrap;
      justify-content: flex-end;
    }
    @include not_last_child{
      margin-right: $margin-s;
      @include sp(){
        &.portfolio{
          width: 100%;
          text-align: right;
          margin-right: 0;
          margin-top: $margin-s;
          order: 3;
        }
      }
    }
  }
}

section.side{
  font-size: $font-s;
  .side_section{
    margin-top: $margin-m;
    margin-bottom: $margin-m;
  }
  .side_head{
    text-align: center;
    padding: 3px 0;
    font-size: $font-s;
    margin-bottom: $margin-m;
    @include background_black();
  }
  .search{
    text-align: center;
    input[type="text"]{
      border: 1px solid $black;
      margin-bottom: $margin-s;
    }
    input[type="submit"]{
      border: 1px solid $black;
      padding: 3px 20px;
      font-size: $font-s;
      margin: 0 auto;
      transition: .1s;
      &:hover{
        @include background_black;
      }
    }
  }
  .author{
    .author_visual{
      margin-bottom: $margin-s;
      img{
        width: 100%;
      }
    }
    .name{
      font-weight: bold;
    }
    .profile{
      margin-bottom: $margin-s;
    }
    .links{
      text-decoration: underline;
      *:not(:last-child){
        margin-right: $margin-s;
      }
      img{
        height: $font-m * 1.1;
      }
    }
  }
  .popular{
    .popular_articles{
      .article{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0 8px;
        @include background_black();
        &:not(:last-child){
          margin-bottom: $margin-s;
        }
        .cats, .date{
          font-size: $font-ss;
          padding: 3px;
        }
        .title{
          width: 100%;
          padding: 3px;
        }
      }
    }
  }
}

footer{
  background-color: $purple;
  width: 100%;
  padding: $margin-m 5vw;
  color: $white;
  font-size: $font-ss;
  @include not_last_child{
    margin-bottom: $margin-s;
  }
  @include sp(){
    text-align: center;
  }
}